<template>
    <div>
        <!-- loading  isLoading -->
        <loading :active.sync="isLoading">
        </loading>

        <div class="text-right mb-3">
            <button class="btn btn-success btn-sm" @click="OpenModal(true)"></button>
        </div>

        <table class="table">
            <thead>
                <tr>
                    <th scope="col" width="80">#</th>
                    <th scope="col"></th>
                    <th scope="col"></th>
                    <th scope="col" width="80"></th>
                    <th scope="col" width="100"></th>
                    <th scope="col"></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(ticket) in coupon_using.coupons" :key='ticket.id'>

                    <th scope="row">{{ticket.num}}</th>
                    <td>{{ticket.title}}</td>
                    <td>{{ticket.due_date}}</td>
                    <td>{{ticket.percent }}</td>
                    <td class="text-center">
                        <span class="text-success" v-if="ticket.is_enabled==1"></span>
                        <span v-else></span>
                    </td>
                    <td>
                        <button class="btn btn-info btn-sm" @click="OpenModal(false, ticket)"></button>
                        <button class="btn btn-warning btn-sm" @click=OpenDelModal(ticket)></button>
                    </td>

                </tr>
            </tbody>
        </table>

        <!-- / Modal -->
        <div class="modal fade" id="couponModal" tabindex="-1" role="dialog">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-dark text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span></span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-sm-12">
                                <div class="form-group">
                                    <label for="title"></label>
                                    <input type="text" class="form-control" id="title" placeholder=""
                                        v-model="coupon_new.title">
                                </div>
                                <div class="form-group">
                                    <div class="form-check">
                                        <input class="form-check-input" type="checkbox" id="is_enabled"
                                            v-model="coupon_new.is_enabled" :true-value="1" :false-value="0">
                                        <label class="form-check-label" for="is_enabled">
                                            
                                        </label>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <label for="title">(1-100%)</label>
                                    <input type="text" class="form-control" id="discount" placeholder="number"
                                        v-model="coupon_new.percent">
                                </div>
                                <div class="form-group">
                                    <label for="title"></label>
                                    <input type="text" class="form-control" id="expiry" placeholder="( Unix Timestamp )"
                                        v-model="coupon_new.due_date">
                                </div>
                                <div class="form-group">
                                    <label for="title"></label>
                                    <input type="text" class="form-control" id="code" placeholder="code"
                                        v-model="coupon_new.code">
                                </div>
                                <hr>

                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-primary" @click='addCoupon(coupon_new.id)'></button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Delete_Modal -->
        <div class="modal fade" id="delModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
            aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-danger text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span></span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                         <strong class="text-danger">{{coupon_new.title}}</strong> ?
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-danger" @click="DelCoupon"></button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>


<script>
    export default {
        data() {
            return {
                isLoading: false, //loading
                coupon_using: {}, //coupon
                coupon_new: {}, //&  coupon
                addingCoupon: false,//
            }
        },
        methods: {

            // 
            getCoupons(page = 1) {
                this.isLoading = true;
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
                const vm = this;

                this.$http.get(api,).then((response) => {
                    console.log(':', response.data)
                    vm.coupon_using = response.data;

                    vm.isLoading = false;

                })
            },

            // coupon
            addCoupon(id) {

                // 
                let ApiMethod = 'post'
                let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
                const vm = this;

                //  method& Api
                if (this.addingCoupon == false) {
                    ApiMethod = 'put';
                    api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${id}`;
                }

                this.$http[ApiMethod](api, { data: vm.coupon_new }).then((response) => {
                    console.log(response.data);
                    $('#couponModal').modal('hide');
                    vm.getCoupons();

                })
            },

            // modal or
            OpenModal(isNew, item) {
                const vm = this;

                if (isNew == true) {
                    vm.coupon_new = {};
                    vm.addingCoupon = true;
                }
                else {
                    vm.coupon_new = Object.assign({}, item);
                    vm.addingCoupon = false;
                }
                $('#couponModal').modal('show');
            },

            // modalmodal
            OpenDelModal(item){
                const vm = this;
                vm.coupon_new = Object.assign({}, item);
                $('#delModal').modal('show');
            },

            // coupon
            DelCoupon() {
                
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${this.coupon_new.id}`;
                const vm = this;

                this.$http.delete(api).then((response) => {
                    console.log(':',response.data);
                    $('#delModal').modal('hide');
                    vm.getCoupons();

                })
            },
        },




        created() {
            this.getCoupons();
        },
    }


</script>


