<template>
  <div class="">

    <!--  -->
    <Alert />


    <!-- Alert -->
    <div class="container py-5 text-center">
      <div class="form-row">
        <div class="col-12 col-sm">
          <div class="alert alert-primary alert-rounded" role="alert">
            1.
          </div>
        </div>
        <div class="col-12 col-sm">
          <div class="alert alert-dark alert-rounded" role="alert">
            2.
          </div>
        </div>
        <div class="col-12 col-sm">
          <div class="alert alert-dark alert-rounded" role="alert">
            3.
          </div>
        </div>
      </div>
    </div>

    <!--  -->

    <div class="row no-gutters justify-content-center">
      <div class="col-md-10 h3 p-4 my-3 text-center bg-light"><strong></strong></div>
    </div>
    <div class="row justify-content-center">
      <div class="col-md-10">
        <div class="card">
          <div class="card-header">
            <button class="btn btn-link" type="button" data-toggle="collapse" data-target="#collapseExample">
              /
            </button>
          </div>
          <div class="collapse card card-body" id="collapseExample">
            <div class="row justify-content-center">
              <div class="table-responsive">
                <table class="table">
                  <thead>
                    <tr>
                      <th scope="col" width="120">#</th>
                      <th scope="col"></th>
                      <th scope="col" width="80"></th>
                      <th scope="col"></th>
                      <th scope="col"></th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(cartItem, index) in cartList.carts">
                      <th scope="row">

                        <span>
                          {{index+1}}.<button class="btn btn-outline-danger btn-sm"
                            @click="delCart(cartItem)"></button>
                        </span>

                      </th>

                      <td>
                        <span class="text-barMain font-weight-bold h5">{{cartItem.product.title}}</span>
                        <br>
                        <span class="text-success" v-if="cartItem.coupon"></span>
                      </td>
                      <td>{{cartItem.qty}}</td>
                      <td>{{cartItem.product.price | currency}}</td>
                      <td>{{cartItem.total | currency}}</td>
                    </tr>
                    <tr>
                      <th scope="row"></th>
                      <td></td>
                      <td></td>
                      <td>:</td>
                      <td>{{cartList.total | currency}}</td>
                    </tr>
                    <tr v-if="cartList.final_total != cartList.total">
                      <th scope="row"></th>
                      <td></td>
                      <td></td>
                      <td class="text-success">:</td>
                      <td class="text-success">{{cartList.final_total | currency}}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>

        <!--  -->
        <div class="row justify-content-center my-3">
          <div class="col-md-12">
            <span class="p-3 font-weight-bold">:</span>
            <div class="p-3 font-weight-bold">5%: <span class="text-barMain font-weight-bold h3">drinkingUp</span></div>
            <div class="input-group mb-3">
              <input type="text" class="form-control" v-model="discountCode" placeholder="CuponCode">
              <div class="input-group-append">
                <button class="btn btn-outline-secondary" type="button" @click="getDiscount"></button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="row no-gutters justify-content-center">
      <div class="col-md-10 h3 p-4 my-3 text-center bg-light"><strong></strong></div>
    </div>

    <div class="my-5 row justify-content-center">

      <form class="col-md-10" @submit.prevent="creatOrder">
        <div class="form-group">
          <label for="useremail">Email</label>
          <input type="email" class="form-control" name="email" id="useremail" v-model="form.user.email"
            placeholder=" Email" required>
          <span class="text-danger">Email Address Please</span>
        </div>

        <div class="form-group">
          <label for="username"></label>
          <input type="text" class="form-control" name="name" id="username" v-model="form.user.name" placeholder=""
            required>
          <span class="text-danger">Do you know your name?</span>
        </div>

        <div class="form-group">
          <label for="usertel"></label>
          <input type="tel" class="form-control" id="usertel" v-model="form.user.tel" placeholder="" required>
        </div>

        <div class="form-group">
          <label for="useraddress"></label>
          <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
            placeholder="" required>
          <span class="text-danger"></span>
        </div>

        <div class="form-group">
          <label for="comment"></label>
          <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
        </div>
        <div class="text-right">
          <button class="btn btn-danger"></button>
        </div>
      </form>
    </div>

    <Index_Footer />

  </div>
</template>

<script>
  import Index_Navbar from '../Index_Navbar';
  import Index_Footer from '../Index_Footer';
  import Alert from '../AlertMessage'; //


  export default {
    name: 'shoppingCart',
    components: {
      Index_Navbar,
      Index_Footer,
      Alert,
    },

    data() {
      return {

        cartList: {
          carts:{}
        },

        discountCode: '', //

        form: { //
          user: {
            name: 'Joy',
            email: 'oioi@gmail.com',
            tel: '0988-989-222',
            address: 'Kaohsiung'
          },
          message: 'Hello Vue'
        },
      }
    },

    methods: {
      // 
      getCart() {
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
        const vm = this;

        this.$http.get(api).then((response) => {
          vm.cartList = response.data.data;
          console.log('', response.data.data);
        })
      },
      //
      getDiscount() {
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
        const vm = this;

        const coupon = { code: vm.discountCode };

        this.$http.post(api, { data: coupon }).then((response) => {

          console.log(response.data)

          if (response.data.success) {
            this.$bus.$emit('message:push', '', 'success')
            vm.getCart();
          }
        })
      },
      delCart(cartItem) {
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${cartItem.id}`;
        const vm = this;

        this.$http.delete(api).then((response) => {

          if (response.data.success) {
            this.getCart();
            this.$bus.$emit('message:push', '', 'danger')
          }
        })
      },

      // 
      creatOrder() {
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
        const vm = this;

        this.$http.post(api, { data: vm.form }).then((response) => {

          console.log('', response.data)

          if (response.data.success) {
            this.$bus.$emit('message:push', '', 'success');
            vm.$router.push(`/customerOrder/${response.data.orderId}`);
          }

        })
      },


    },

    created() {
      this.getCart()
    },

  }

</script>


<style>

</style>


