<script>
    
    import Index_Navbar from './Index_Navbar';
    import Index_Footer from './Index_Footer';
    import Alert from './AlertMessage';// Alert Message

    export default {
        name: 'index',
        components: {
            Index_Navbar,
            Index_Footer,
            Alert
        },
        data() {
            return {
                cartList: {
                    carts:{}
                },

            }
        },
        methods: {
            signout() {
                const api = `${process.env.APIPATH}/logout`;
                const vm = this;

                this.$http.post(api).then((response) => {
                    console.log(response.data);
                    vm.$router.push('/login')
                })

            },
            addtoCart(id, qty = 1) {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
                const vm = this;

                // 
                const addingItem = {
                    product_id: id,
                    qty: qty,
                };

                this.$http.post(api, { data: addingItem }).then((response) => {
                    console.log(response.data);

                    vm.getCart();
                    $('#productModal').modal('hide')
                    this.$bus.$emit('message:push', '', 'success')
                })
            },
            getCart() {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
                const vm = this;

                this.$http.get(api).then((response) => {
                    vm.cartList = response.data.data;
                    console.log('', response.data.data);
                })
            },


        },

        created() {
            this.getCart();
        },
    }

</script>

