<template>
  <div class="">

    <!--  -->
    <loading :active.sync="isLoading">
    </loading>


    <!-- NavBar -->
    <!-- <Index_Navbar :shoppingList='cartList'></Index_Navbar> -->

    <!-- v-for -->
    <div class="row mt-4">
      <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
        <div class="card border shadow-sm">
          <div style="height: 280px; background-size: cover; background-position: center center"
            :style="{backgroundImage: `url(${item.imageUrl})`}">
          </div>
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
            <h5 class="card-title">
              <a href="#" class="text-barMain font-weight-bold">{{ item.title }}</a>
            </h5>
            <p class="card-text">{{ item.content }}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h5" v-if="!item.price">{{ item.origin_price | currency}} </div>
              <del class="h6" v-if="item.price"> {{ item.origin_price | currency}} </del>
              <div class="h5" v-if="item.price"> {{ item.price | currency}} </div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <button type="button" class="btn btn-outline-secondary btn-sm" @click="getSingleProduct(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem==item.id"></i>
              
            </button>
            <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addtoCart(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem==item.id"></i>
              
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- ""Modal -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
      aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" alt="productPhoto">
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">{{ product.description }}</footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">{{ product.origin_price }} </div>
              <del class="h6" v-if="product.price"> {{ product.origin_price }} </del>
              <div class="h4" v-if="product.price"> {{ product.price }} </div>
            </div>
            <select name="qty" class="form-control mt-3" v-model="product.Qty">
              <option value="0" disabled selected></option>
              <option :value="num" v-for="num in 6" :key='num'>{{ num }} {{ product.unit}}</option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
               <strong>{{ product.Qty * product.price }}</strong> 
            </div>
            <button type="button" class="btn btn-primary" @click="addtoCart(product.id, product.Qty)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem==product.id"></i>
              
            </button>
          </div>
        </div>
      </div>>
    </div>


  </div>
</template>

<script>
  import Index_Navbar from '../Index_Navbar';


  export default {
    name: 'Whiskey',
    components: {
      Index_Navbar,
    },
    data() {
      return {
        products: {}, //data

        product: {}, //data

        isLoading: false, //loading

        status: {
          loadingItem: '', //icon/
        },

      }
    },

    methods: {
      //
      getProducts() {
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products/all`;
        // const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products/all`;
        // const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products`;
        // const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`;
        const vm = this;

        vm.isLoading = true; //

        this.$http.get(api).then((response) => {

          console.log(':', response.data);
          vm.isLoading = false; // 
          vm.products = response.data.products;

        })
      },
      //
      getSingleProduct(id) {

        this.status.loadingItem = `${id}`; //

        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
        const vm = this;

        this.$http.get(api).then((response) => {
          console.log('', response.data);
          vm.product = response.data.product;

          vm.status.loadingItem = '' //
          $('#productModal').modal('show');

        })
      },
      // 
      addtoCart(id, qty = 1) {
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
        const vm = this;
        this.status.loadingItem = `${id}`; //

        // 
        const addingItem = {
          product_id: id,
          qty: qty,
        };

        this.$http.post(api, { data: addingItem }).then((response) => {
          console.log(response.data);

          vm.status.loadingItem = '' //

          $('#productModal').modal('hide')
          this.$bus.$emit('message:push', '', 'success')

        })

      },

    },

    created() {
      this.getProducts();

      // event bus
      this.$bus.$emit('message:push', 'HelloWorld', 'info')
    },


  }

</script>


<style>
  @import "../../assets/helpers/service_whiskey_style.sass";
</style>


