<template>
    <div>

        <!-- loading  isLoading-->
        <loading :active.sync="isLoading">
        </loading>

        <table class="table">
            <thead>
                <tr>
                    <th scope="col" width="20">#</th>
                    <th scope="col"></th>
                    <th scope="col"></th>
                    <th scope="col"></th>
                    <th scope="col" width="80"></th>
                    <th scope="col" width="100"></th>
                    <th scope="col"></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in orders">

                    <th scope="row">{{item.num}}</th>

                    <td>{{item.create_at}}</td>

                    <td>
                        <ul>
                            <li v-for='each_product in item.products'>
                                {{each_product.product.title}} {{each_product.qty}} {{each_product.product.unit}}
                            </li>
                        </ul>
                    </td>

                    <td>
                       <ul>
                        <li v-for='each_User in item.user'>
                            {{each_User}}
                        </li>
                       </ul>
                    </td>
                    
                    <td class="text-center">
                        <span class="text-success" v-if="item.is_paid == true"></span>
                        <span v-else></span>
                    </td>

                    <td>{{item.total}}</td>

                    <td>{{item.message}}</td>

                </tr>
            </tbody>
        </table>


        <!-- Pagination -->
        <!-- :pagi_datapagination -->
        <!-- emitgetProducts -->
        <!-- <Pagination :pagi_data='pagination' v-on:emit_function="getProducts"></Pagination> -->


    </div>
</template>


<script>

    import Pagination from '../Pagination';

    export default {
        components: {
            Pagination,
        },
        data() {
            return {
                orders:{},
                isLoading: false, //loading
                pagination: {},
            }
        },
        methods: {
            getOrders(page=1) {
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/orders?page=${page}}`;
                const vm = this;

                vm.isLoading = true; //  ()

                this.$http.get(api).then((response) => {

                    console.log(response.data);
                    vm.orders = response.data.orders;
                    vm.isLoading = false; //  ()

                    // vm.pagination = response.data.pagination;

                })

            },
        },


        created() {
            this.getOrders();
            this.$bus.$emit('message:push', 'Message', 'info') //event bus
        },

    }
</script>


